<template>

       <b-container>
           <h5 class="h3 mb-3 font-weight-normal" style="text-align: center"> Sign in</h5>
            <b-container class="modal-footer">
            </b-container>
            <b-row>
                <b-col class="col text-left">
                    <validation-provider
                        name="Email - Login"
                        :rules="{ required: true, min: 3 }"
                        v-slot="validationContext"
                    >
                        <b-form-group id="input-group-email-login" label="Email - Login" label-for="input-email-login">
                            <b-form-input
                                id="input-login-email"
                                name="input-login-email"
                                v-model="input.username"
                                placeholder="Email Address"
                                :state="getValidationState(validationContext)"
                                aria-describedby="input-1-live-feedback"
                                autofocus
                            ></b-form-input>
                            <b-form-invalid-feedback id="input-1-live-feedback">{{ validationContext.errors[0] }}</b-form-invalid-feedback>
                        </b-form-group>
                    </validation-provider>
                </b-col>
            </b-row>
            <b-row>
                <b-col  class="col text-left">
                    <validation-provider
                        name="Password"
                        :rules="{ required: true, min: 3 }"
                        v-slot="validationContext"
                    >
                        <b-form-group id="input-group-password" label="Password" label-for="input-password">
                            <b-form-input
                                id="input-password"
                                name="input-password"
                                v-model="input.password"
                                placeholder="Password"
                                type="password"
                                :state="getValidationState(validationContext)"
                                aria-describedby="input-1-live-feedback"
                            ></b-form-input>
                            <b-form-invalid-feedback id="input-1-live-feedback">{{ validationContext.errors[0] }}</b-form-invalid-feedback>
                        </b-form-group>
                    </validation-provider>
                </b-col>
            </b-row>
            <b-row>
                <b-col>
                    <b-form-group id="input-login" label-for="input-login">
                        <b-button variant="success" size="sm" v-on:click="login()">Login</b-button>
                    </b-form-group>
                </b-col>
            </b-row>
            
            
     </b-container>
</template>

<script>
    import firebase from 'firebase'

    export default {
        name: 'Login',
        data() {
            return {
                input: {
                    username: "",
                    password: ""
                }
            }
        },
        methods: {
             getValidationState({ dirty, validated, valid = null }) {
                return dirty || validated ? valid : null;
            }, 
            login() {
                if(this.input.username != "" && this.input.password != "") {
                    
                    firebase.auth()
                        .signInWithEmailAndPassword(this.input.username, this.input.password)
                        .then(data => {
                            this.$parent.authenticated = true 
                            this.$router.replace({ name: "Home" })     
                            //this.$router.replace({ name: "Dashboard" });
                        })
                        .catch(err => {
                            
                            console.log(err.message)
                            this.$bvModal.msgBoxOk(err.message, {
                                title: 'Error',
                                size: 'sm',
                                buttonSize: 'sm',
                                okVariant: 'danger',
                                headerClass: 'p-2 border-bottom-0',
                                footerClass: 'p-2 border-top-0',
                                centered: true
                                })
                                .then(value => {
                                    this.boxTwo = value
                                })
                                .catch(err => {
                                    // An error occurred
                                })
                        });

                 
                } else {
                    console.log("A username and password must be present");
                }
            }
        }
    }
</script>

<style scoped>
   div {
	width: 300px;
}
</style>